.api-result {
  background-color: #777;
  color: white;
}

.border-right {
  border-right: 1px solid rgba(0, 0, 0, 0.12);
}
.v-window {
  height: 100% !important;
}

.border-radius-1 {
  border-radius: 10px !important;
}
.border-radius-0 {
  border-radius: 5px !important;
}
.overflow-hidden {
  overflow: hidden;
}

tr {
  cursor: pointer;
}

.grey-bg {
  background-color: rgb(233, 233, 233);
}
.jsontree_bg {
  background: #fff;
}
